@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@200&display=swap');

.mainbackground {
	min-height: 93vh;
	display: flex;
	padding: 10px 0;
}

.page {
	width: 100%;
}

.heading {
	font-size: 40px;
	font-family: 'Work Sans, sans-serif';
	padding: 5px 10px;
}

@media only screen and (max-width: 600px) {
	.heading {
		font-size: 30px;
		display: flex;
		justify-content: center;
	}
}
